@import "./sass/styles.scss";

.App {
    text-align: center;
}

.App-logo {
    height: 0vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-link {
    color: #61dafb;
}

.blue {
    background-color: $blue;
}

.red {
    background-color: $red;
}

.neutral {
    background-color: $neutral;
    color: black;
    font-weight: 500;
}

.black {
    background-color: $black;
    color: white;
}

@mixin back-background-image($type) {
    background-image: url("./assets/images/back-#{$type}-men.png");

    &.men {
        background-image: url("./assets/images/back-#{$type}-men.png");
    }

    &.lady {
        background-image: url("./assets/images/back-#{$type}-lady.png");
    }
}

@mixin background-image($position, $type) {
    background-image: url("./assets/images/#{$position}-#{$type}.png");
}

@mixin background-image-types($type, $bgColor, $fontColor) {
    @include background-image("front", $type);
    background-color: $bgColor;
    color: $fontColor;
}

.card {
    /* height: 6rem; */
    text-align: center;
    width: 20vmin;
    border: 1px;
    margin: 1.2vmin;
    padding: 2vmin 0;
    border-radius: 0.5rem;
    box-shadow: 2px 2px 5px 2px grey;
    // border-width: 0.75rem;
    // border-style: solid;
    // border-color:bisque;

    &:hover:not(.disabled) {
        transform: scale(1.15);
        transition-duration: 1s;
    }

    @media only screen and (max-width: 480px) {
        width: 18vmin;
        padding: 1.5vmin 0;
    }

    @media only screen and (min-width: 980px) {
        padding-top: 2.5vmin;
    }

    &.image {
        @include background-image-types("neutral", $neutral, black);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;

        // &.blue {
        //     @include background-image-types("blue", $blue, black)
        // }

        // &.red {
        //     @include background-image-types("red", $red, black)
        // }

        // &.neutral {
        //     @include background-image-types("neutral", $neutral, black)
        // }

        // &.black {
        //     @include background-image-types("black", $black, white)
        // }

        &.overlay {
            opacity: 1;
            color: transparent;

            &.blue {
                @include back-background-image("blue");
            }

            &.red {
                @include back-background-image("red");
            }

            &.neutral {
                @include back-background-image("neutral");
            }

            &.black {
                @include background-image("back", "black");
            }

            &.open {
                // transform: rotateX(55deg) translateZ(42px);
                color: white;
            }

            &.close {
                // transform: rotateX(0deg);
                color: transparent;
            }
        }

        .text {
            padding: 0;
            padding-top: 5.5vmin;
            font-size: calc(5px + 1.5vmin);
            font-weight: bold;

            @media only screen and (max-width: 480px) {
                font-size: calc(3px + 1.5vmin);
            }
        }
    }

    .text {
        padding: 2vmin 0;
        font-size: calc(5px + 2vmin);
        font-weight: 500;
    }

    &.clicked {
        opacity: 0.3;
    }

    &.rotate:not(.disabled) {
        transform: scale(1) rotateY(360deg);
        transition-duration: 3s;

        &:hover {
            transform: scale(1) rotateY(360deg);
            transition-duration: 3s;
        }
    }
}

.board {
    display: grid;
    &.pattern-25 {
        grid-template-columns: auto auto auto auto auto;
    }
    &.pattern-36 {
        grid-template-columns: auto auto auto auto auto auto;
    }
    &.pattern-49 {
        grid-template-columns: auto auto auto auto auto auto auto;
    }
    &.pattern-64 {
        grid-template-columns: auto auto auto auto auto auto auto;
    }
    &.pattern-81 {
        grid-template-columns: auto auto auto auto auto auto auto;
    }
}

main {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    // justify-content: center;
    font-size: calc(5px + 2vmin);
    color: white;

    @media only screen and (max-width: 480px) {
        min-height: 75vh;
        font-size: calc(3px + 2vmin);
    }
}

// header {
//     background-color: $background-color;
// }

// footer {
//     background-color: $background-color;
// }

.capitalize {
    text-transform: capitalize;
}

.flex {
    display: flex;

    &.space-between {
        flex-direction: row;
        justify-content: space-between;

        &.responsive {
            @media only screen and (max-width: 480px) {
                flex-direction: column;
            }

            @media only screen and (max-height: 480px) {
                flex-direction: column;
            }
        }
    }
}

.disabled {
    opacity: 0.3;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}
